.table-bordered {
    width: 100% !important;
  }
  
  .Toastify__progress-bar--success {
    background: #07bfb5;
  }
  .Toastify__toast--success svg {
    fill: #07bfb5 !important;
  }
  
  .dropdown-container {
    position: relative;
    display: flex;
    align-items: center;
  }
  
  .dropdown-card {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background-color: #fff;
    border-radius: 0 0 10px 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    z-index: 1;
  }
  
  .dropdown-item {
    padding-left: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    cursor: pointer;
  }
  
  .dropdown-item:hover {
    background-color: #f0f0f0;
  }
  